import React from "react"
import { Link } from "gatsby"
import styled, { css } from "react-emotion"

const Wrapper = styled("header")`
  height: 120px;
`

const BlogNameStyle = css({
  display: 'inline-block',
  color: '#838383',
  fontSize: '28px',
  marginTop: '8px',
})

const MenuItem = css`
  color: #E0E0E0;
  margin: 0 8px;
  &:hover {
    color: #FAAA5E;
  }
`

const Menu = styled("div")`
  color: #E0E0E0;
  float: right;
  margin: 18px 0;
`

export default () => { 
  return (
    <Wrapper className='wrapper'>
      <Link className={BlogNameStyle} to={`/`}>eeearl</Link>
      <Menu>
        <Link className={MenuItem} to={`/about`}>about</Link>
        <Link className={MenuItem} to={`/blog`}>blog</Link>
      </Menu>
    </Wrapper>
  ) 
}